.card {
  background-color: var(--base-100);
  border-radius: 60px;
  flex-direction: column;
  justify-content: space-between;
  height: 640px;
  padding: 40px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.dm-gsap-cards {
  transform-origin: top;
  will-change: transform;
  position: sticky;
  top: 0;
}

.card:before {
  content: "";
  filter: blur(20px);
  z-index: 0;
  background: linear-gradient(360deg, #0505054d 0%, #050505cc 100%);
  width: 100%;
  height: 40%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.card__header {
  z-index: 10;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.card__advantages {
  flex-wrap: wrap;
  align-items: center;
  gap: 4px;
  display: flex;
}

.card-3 .card__advantages {
  gap: 20px;
}

.card__advantage {
  -webkit-backdrop-filter: blur(40px);
  text-transform: uppercase;
  text-wrap: nowrap;
  white-space: nowrap;
  background: #ffffff1a;
  border-radius: 20px;
  outline: 1px solid #fff3;
  justify-content: center;
  align-items: center;
  height: 50px;
  padding: 20px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1;
  display: flex;
  position: relative;
  box-shadow: 0 4px 30px #0000001a;
}

.card__content {
  z-index: 10;
  margin-top: 80px;
  position: relative;
}

.card-4 .card__content {
  margin: auto 0;
  transform: translateY(36px);
}

.card__title {
  font-family: var(--font-family-third);
  letter-spacing: .02em;
  -webkit-text-fill-color: transparent;
  background: linear-gradient(90deg, #fff 0%, #5b6468 100%) text;
  width: 100%;
  margin-bottom: 65px;
  font-size: 48px;
  font-weight: 500;
  line-height: 120%;
}

.card.card-1 {
  z-index: 10;
}

.card.card-2 {
  z-index: 11;
}

.card.card-3 {
  z-index: 12;
}

.card.card-4 {
  z-index: 13;
}

.card.card-1 .card__title {
  max-width: 420px;
}

.card.card-2 .card__title {
  max-width: 620px;
}

.card.card-3 .card__title {
  max-width: 720px;
}

.card.card-4 .card__title {
  max-width: 970px;
}

.card.orange-card:before {
  filter: blur(40px);
  background: linear-gradient(#131313 0%, #000 100%);
}

.card:after {
  content: "";
  filter: blur(12px);
  z-index: 5;
  background: linear-gradient(0deg, #05050599 0% 100%);
  height: 30%;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.card.orange-card:after {
  filter: blur(20px);
  background: linear-gradient(360deg, #ff7b2226 0%, #ff7b220f 100%);
  height: 30%;
}

.orange-card .card__title {
  -webkit-text-fill-color: transparent;
  background: linear-gradient(61deg, #fff 0%, #ff5f20 100%) text;
}

.card__bg {
  object-fit: cover;
  object-position: center;
  position: absolute;
  inset: 0;
}

.card__bg img {
  width: 100%;
  height: 100%;
  display: block;
}

.card.card-2 .card__bg {
  opacity: .4;
}

.card__image {
  width: 100%;
  max-width: 1040px;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
}

.card.card-2 .card__image {
  max-width: 651px;
}

.card.card-3 .card__image {
  max-width: 950px;
}

.card__footer {
  z-index: 10;
  align-items: end;
  gap: 60px;
  margin-top: auto;
  display: flex;
  position: relative;
}

.card-4 .card__footer {
  margin-top: unset;
  gap: 105px;
}

.card__footer-button.button {
  text-transform: none;
  height: 58px;
  padding: 24px 28px;
  transform: translateY(-6px);
}

.card.orange-card .card__footer-button.button {
  -webkit-backdrop-filter: blur(40px);
  background-color: #ff5f201a;
}

.card.orange-card.card-4 .card__footer-button.button {
  background-color: #ff5f2033;
}

.card__parameters {
  columns: 2;
  width: 100%;
  max-width: 820px;
}

.card-3 .card__parameters {
  column-gap: 126px;
}

.card-1 .card__parameters {
  columns: 1;
}

.card__parameter {
  color: #fffc;
  text-wrap: nowrap;
  margin-bottom: 14px;
  margin-left: 24px;
  font-size: 16px;
  font-weight: 400;
  line-height: 140%;
  list-style: outside;
}

.card__parameter::marker {
  color: var(--green-100);
  font-size: 20px;
}

.orange-card .card__parameter::marker {
  color: var(--orange-100);
}

.card__footer-description {
  font-family: var(--font-family-third);
  letter-spacing: .02em;
  -webkit-text-fill-color: transparent;
  background: linear-gradient(90deg, #fff 0%, #5b6468 100%) text;
  font-size: 28px;
  font-weight: 500;
  line-height: 120%;
}

.card.orange-card .card__footer-description {
  -webkit-text-fill-color: transparent;
  background: linear-gradient(61deg, #fff 0%, #ff5f20 100%) text;
}

.card__header-description {
  font-family: var(--font-family-first);
  color: var(--base-content-100);
  opacity: .6;
  font-size: 16px;
  font-weight: 400;
  line-height: 140%;
}

.card-3 .card__header-description {
  max-width: 160px;
}

.card__video-wrapper {
  opacity: .4;
  position: absolute;
  inset: 0;
}

.card__video {
  object-fit: cover;
  object-position: top center;
  width: 100%;
  height: 100%;
}

@media (width <= 1350px) {
  .card.card-3 .card__image {
    right: -5%;
  }
}

@media (width <= 1280px) {
  .card.card-3 .card__image {
    right: -8%;
  }
}

