    max-width: 80%;
  }

  .card.card-2 .card__image {
    max-width: 60%;
    right: 3%;
  }

  .card.card-3 .card__image {
    max-width: 100%;
    right: 0;
  }

  .card__footer-button.button {
    height: auto;
    padding: 10px 32px;
  }

